<script>
	import db from "./firebase_config/FirebaseInit";
	import firebase from 'firebase';
	import pdf from 'vue-pdf';

	export default {
		name: "dashboard",
		components: {
			pdf
		},
		data() {
			return {
				users: {
					pdf_id: '',
					full_name: '',
					admission_no: '',
					phone_no: '',
					path_to_file: '',
					pdf_url: '',
					pdf_file_props: {
						color: {
							value: 'NO',
							color_pages: ''
						},
						number_of_copies: '1',
						double_sided: "",
						binding: {
							value: '',
							binding_type: '',
							cost: ''
						}
					},
					pending: true,
					cost: ''
				},
				pdf_size: '',
				imageData: null,
				picture: null,
				uploadValue: 0,
				currentPage: 0,
				pageCount: 0
			}
		},
		created() {
			// db.collection("users").get().then(querySnapshot => {
			// 	querySnapshot.forEach(doc => {
			// 		// console.log(doc.data())
			// 		// const data = {
			// 		// 	"id": doc.id,
			// 		// 	"employee_id": doc.data().employee_id,
			// 		// 	"name": doc.data().name,
			// 		// 	"dept": doc.data().dept,
			// 		// 	"position": doc.data().position
			// 		// }
			// 		// this.employees.push(data)
			// 		console.log(doc.data())
			// 		// this.users.full_name = doc.data().name;
			//
			// 	});
			// })


		},
		methods: {
			previewImage(event) {
				this.uploadValue = 0;
				this.picture = null;
				this.imageData = event.target.files[0];

				console.log(this.imageData)


				// ONLY WORKS FOR URL NOT FOR LOCAL FILES
				// let loadingTask = pdfjsLib.getDocument('https://raw.githubusercontent.com/mozilla/pdf.js/ba2edeae/examples/learning/helloworld.pdf');
				// loadingTask.promise.then(function (pdf) {
				// 	let numPages = pdf.numPages;
				// 	console.log('# Document Loaded');
				// 	console.log('Number of Pages: ' + numPages);
				//
				// });


				const fi = document.getElementById('file');
				// Check if any file is selected.
				if (fi.files.length > 0) {

					console.log(fi.files.item(0).size)

					console.log(fi.files.length)

					for (let i = 0; i <= fi.files.length - 1; i++) {
						// Get the Bytes
						const fsize = fi.files.item(i).size;

						// Convert to MB
						this.pdf_size = (fsize / (1024 * 1024)).toFixed(2);
						// The size of the file.
						if (this.pdf_size >= 4096) {
							alert(
								"File too Big, please select a file less than 4mb");
						} else {
							document.getElementById('file_size').innerHTML = 'File Size Approx: ' + '<b>'
								+ this.pdf_size + '</b> MB';
						}
					}
				}
			},
			onUpload() {

				if (this.imageData && this.users.admission_no !== '') {

					document.getElementById("progress_bar").style.display = "block"

					db.collection("users").get().then(res => {
						console.log(res.size);
						this.users.pdf_id = res.size + 1;

						db.collection('users').add({
							pdf_id: this.users.pdf_id.toString(),
							full_name: this.users.full_name,
							admission_no: this.users.admission_no,
							phone_no: firebase.auth().currentUser.phoneNumber,
							path_to_file: this.users.path_to_file,
							pdf_name: this.imageData.name,
							pdf_file_props: {
								color: {
									value: this.users.pdf_file_props.color.value,
									color_pages: this.users.pdf_file_props.color.color_pages
								},
								number_of_copies: this.users.pdf_file_props.number_of_copies,
								double_sided: this.users.pdf_file_props.double_sided,
								binding: {
									value: this.users.pdf_file_props.binding.value,
									binding_type: this.users.pdf_file_props.binding.binding_type,
									cost: this.users.pdf_file_props.binding.cost
								}
							},
							pdf_size: this.pdf_size + "MB",
							pending: this.users.pending,
							cost: this.users.cost

						}).then(docRef => {

							this.picture = null;
							this.users.path_to_file = firebase.auth().currentUser.uid + "/" + docRef.id + "/" + `${this.imageData.name}`;
							const storageRef = firebase.storage()
								.ref(this.users.path_to_file).put(this.imageData);

							storageRef.on(`state_changed`, snapshot => {
									this.uploadValue = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
								}, error => {
									console.log(error.message)
								},
								() => {
									this.uploadValue = 100;

									storageRef.snapshot.ref.getDownloadURL().then((url) => {
										this.picture = url;
										console.log(this.picture);

										db.collection('users').doc(docRef.id).update({
											path_to_file: this.users.path_to_file,
											pdf_url: url
										});

										alert("File Uploaded!!");
										document.getElementById("progress_bar").style.display = "none"
										window.location.reload();
									});
								}
							);
						}).catch(err => {
							console.log(err);
						});
					});
				}

			},
			checkColor() {
				let val = document.getElementsByName("color_val")
				for (let i = 0; i < val.length; i++) {
					if (val[i].checked) {
						this.users.pdf_file_props.color.value = val[i].value;
						console.log("Value is: " + this.users.pdf_file_props.color.value);
					}
				}
			},
			checkDoubleSided() {
				let val = document.getElementsByName("double_side")
				for (let i = 0; i < val.length; i++) {
					if (val[i].checked) {
						this.users.pdf_file_props.double_sided = val[i].value;
						console.log("Value is: " + this.users.pdf_file_props.double_sided);
					}
				}
			},
			checkBinding() {
				let val = document.getElementsByName("binding_value")
				for (let i = 0; i < val.length; i++) {
					if (val[i].checked) {
						this.users.pdf_file_props.binding.value = val[i].value;
						console.log("Value is: " + this.users.pdf_file_props.binding.value);
					}
				}
				this.checkBindingDetail();
			},
			checkBindingDetail() {
				let val2 = document.getElementsByName("binding_val")
				for (let i = 0; i < val2.length; i++) {
					if (val2[i].checked) {

						if (val2[i].id === "soft_binding_yes") {
							this.users.pdf_file_props.binding.binding_type = "Soft Binding";
							this.users.pdf_file_props.binding.cost = "10";
						} else if (val2[i].id === "soft_binding_no") {
							this.users.pdf_file_props.binding.binding_type = "";
						} else if (val2[i].id === "hard_binding_no") {
							this.users.pdf_file_props.binding.binding_type = "";
						} else if (val2[i].id === "stick_binding_no") {
							this.users.pdf_file_props.binding.binding_type = "";
						} else if (val2[i].id === "hard_binding_yes") {
							this.users.pdf_file_props.binding.binding_type = "Hard Binding";
							this.users.pdf_file_props.binding.cost = "15";
						} else if (val2[i].id === "stick_binding_yes") {
							this.users.pdf_file_props.binding.binding_type = "Stick Binding";
							this.users.pdf_file_props.binding.cost = "5";
						}
						console.log("Value is: " + this.users.pdf_file_props.binding.binding_type);
					}
				}
			}
		}
	}
</script>

